import React, {useRef, useEffect} from 'react';
import {ScrambledTextProps} from ".";
import s from './ScrambledText.module.scss';

export const ScrambledText: React.FC<ScrambledTextProps> = ({
  value,
  slideLength = 4000}) => {

  const elRef = useRef<HTMLDivElement>(null);
  let chars = '![]#____________![]#____________';
  let resolved: any;
  let queue: any[] = [];
  let frameRequest: number;
  let frame = 0; //
  const phrases = value;

  let counter = 0;


  useEffect(() => {
    next();
    // eslint-disable-next-line
  }, []);

  //     ,  counter,  counter     counter  0
  const next = () => {
    setText(phrases[counter])
      .then(() => {
        setTimeout(next, slideLength);
      });
    counter = (counter + 1) % phrases.length;
  }

  //  
  const setText = (newText: string) => {
    let content   = (elRef.current && elRef.current.innerText) || ''; //    
    const length  = Math.max(content.length, newText.length);         //   
    const promise = new Promise((resolve, _reject) => resolved = resolve );    //  resolved 
    queue = []; //   

    for (let i = 0; i < length; i++) {
      const from   = content[i] || '';
      const to     = newText[i] || '';
      const start  = Math.floor(Math.random() * 40);          // start  end    
      const end    = start + Math.floor(Math.random() * 40);  //  0  80,  [0,2], [10,25], [40, 80]
      queue.push({ from, to, start, end });
    }

    cancelAnimationFrame(frameRequest);
    frame = 0; //    0
    update();
    return promise;
  }

  //   ()   queue
  //     queue ,  ,  char  queue[i]
  //      
  const update = () => {
    let output = '';  //   
    let complete = 0; // .   = queue.length

    //     ,      .
    //    ,  ( )   ,  ""  ""
    for (let i = 0, n = queue.length; i < n; i++) {
      let {from, to, start, end, char} = queue[i];

      if (frame >= end) {
        complete++;
        output += to;
      } else if (frame >= start) {
        if (!char || Math.random() < 0.28) {
          char = randomChar();
          queue[i].char = char;
        }
        // TODO: move class or styles to component scope. Now you can find class definition in the addons.scss
        output += `<span class="dud">${char}</span>`;
        // output += `${char}`;

      } else {
        output += from;
      }
    }

    //     DOM
    if (elRef.current) {
      elRef.current.innerHTML = output;
    }

    //     queue,     ,   .
    complete === queue.length ? resolved()
      : frameRequest = requestAnimationFrame(update);
        frame++;

  }

  const randomChar = () => {
    return chars[Math.floor(Math.random() * chars.length)];
  }

  return (
    <div className={s.ScrambledTextContainer}>
      <div ref={elRef} className={s.ScrambledText}>

      </div>
    </div>
  );
}
